/* --- typography --- */
body {
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  font-weight: 400;
  color: #798696;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 12px;
  margin-bottom: 15px;
  font-weight: 600;
  color: #374050;
}

h1 {
  font-size: 28px;
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 24px;
}

h4 {
  font-size: 18px;
}

a {
  font-family: "Lato", sans-serif;
  color: #374050;
  font-weight: 700;
}

a:hover,
a:focus {
  text-decoration: none;
  outline: none;
  color: #374050;
