.navbar .nav-toggle {
  display: block;
  width: 32px;
  height: 32px;
  cursor: pointer;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  position: absolute;
  z-index: 2;
  right: 0;
}

.navbar .nav-toggle span {
  display: block;
  width: 100%;
  height: 2px;
  background: #000;
  position: absolute;
  left: 0;
  -webkit-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
}
.navbar .nav-toggle span:nth-child(1) {
  top: 4px;
  -webkit-transform-origin: left center;
  transform-origin: left center;
}
.navbar .nav-toggle span:nth-child(2) {
  top: 14px;
  -webkit-transform-origin: left center;
  transform-origin: left center;
}
.navbar .nav-toggle span:nth-child(3) {
  top: 24px;
  -webkit-transform-origin: left center;
  transform-origin: left center;
}
.navbar .nav-toggle.open span:nth-child(1) {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  top: 3px;
  left: 4px;
}
.navbar .nav-toggle.open span:nth-child(2) {
  width: 0;
  opacity: 0;
}
.navbar .nav-toggle.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  top: 25px;
  left: 4px;
}
.navbar .nav-layer {
  position: absolute;
  top: 16px;
  left: 16px;
  z-index: -1;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 50%;
  -webkit-transition: 1s;
  transition: 1s;
  -webkit-transform: scale3d(0, 0, 0);
  transform: scale3d(0, 0, 0);
}
